  .page-nav__li::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -0.15em;
    transform: translate(0, -50%);
  }
  .page-nav__li:last-child::after {
    display: none;
  }
}

.page-nav__item {
  display: block;
  color: inherit;
  text-decoration: none;
  text-transform: none;
  transition: opacity 0.2s ease;
  padding: 0.5em 1em;
}

.page-nav__item:hover {
  opacity: 0.7;
  border-bottom: .10em solid #1e87f0;
}

@media (min-width: 48em) {
  .page-nav__item {
    padding: 0.825em 2.5em;
  }
}

@media (min-width: 60em) {
  .page-nav__item {
    padding: 0.825em 3.2em;
  }
}

.page-nav__hamburger {
  display: inline-block;
  cursor: pointer;
  transition: transform 0.2s ease;
  margin-bottom: 1em;
  padding: 0 1em;
}

.page-nav__hamburger:hover {
  transform: scale(1.2);
}

@media (min-width: 48em) {
  .page-nav__hamburger {
    display: none;
  }
}

.page-nav__ul {
  display: none;
}

@media (min-width: 48em) {
  .page-nav__ul {
    display: block !important;
  }
}

.page-nav [aria-current] {
