.flex {
  display: -ms-flexbox;
  display: flex;
  flex: 1;
}

.flex-grid {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.flex-grid {
  margin-left: 16px;
  margin-right: 16px;
}

.flex-middle {
  align-items: center;
}

.flex-first {
  order: -1;
}

.flex-last {
  order: 99;
}

