.m-search {
  position: absolute;
  max-width: 100%;
  top: 17%;
  right: 2%;
}

.m-search-wrapper {
  position: absolute;
  right: inherit;
  width: 44px;
  height: 44px;
}

.m-search-wrapper.open {
  width: 170px;
}

.m-search__input {
  position: relative;
  background-color: #3c464c;
  height: 44px;
  line-height: 26px;
  padding: 5px 10px 5px 37px;
  box-sizing: border-box;
  border: 0;
  border-radius: 100px;
  margin: 0;
  width: 100%;
}

.m-search__input:focus {
  outline: none;
  border-color: #aaa;
}

.m-search__button {
  position: absolute;
  top: -1px;
  left: -1px;
  border: 0;
  padding: 0;
  margin: 0;
  background-color: transparent;
  color: #ddd;
  width: 44px;
  height: 44px;
  text-align: center;
}

.m-search__button:focus {
  outline: none;
}

.m-search__input:focus, .m-search__button,
.m-search__button:hover {
  color: #aaa;
}

.m-card-body::before, .m-card-body::after, .m-card-header::before, .m-card-header::after, .m-card-footer::before, .m-card-footer::after {
  content: "";
  display: table;
}

.m-card-body::after, .m-card-header::after, .m-card-footer::after {
  clear: both;
}

.m-card {
  position: relative;
  box-sizing: border-box;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  transition: box-shadow .1s ease-in-out;
}

.m-card a {
  text-decoration: none;
}

.m-card-default {
  background: #fff;
  color: #666;
}

.m-card-primary .m-card-header {
  background-color: #ddd;
}

.m-card-header {
  padding: 16px 0;
}

.m-card-title {
  font-size: 1em;
  line-height: 1.4;
  color: #222;
  font-weight: bold;
  margin-bottom: 1em;
}

.m-card-body {
  padding: 30px 20px;
  font-weight: 600;
}

.m-card-body > * {
  flex: 1 0 auto;
}

.m-card-body .m-card-title {
  flex: 0 0 auto;
}

.m-card-body .m-card-footer {
  flex: 0 0 auto;
}

.m-text-meta {
  font-size: 0.875em;
  line-height: 1.4;
  color: #999;
}

.m-card-primary .m-text-meta {
  color: #fff;
}

.m-card > .m-card-footer {
  padding: 16px 16px;
  flex: 0 0 auto;
}

.m-card-footer {
  flex: 0 0 auto;
}

.m-card-primary .m-card-header {
  background-color: #ddd;
}

