  display: flex;
  flex-wrap: wrap;
  padding: 0;
  list-style: none;
  margin-bottom: 1.5em;
}

.m-breadcrumb > * {
  flex: none;
}

.m-breadcrumb > * > * {
  display: inline-block;
  font-size: 1em;
  color: #999;
}

.m-breadcrumb > :nth-child(n+2):not(.first-column)::before {
  content: ">";
  display: inline-block;
  margin: 0 15px;
  color: #999;
}

